.backupInfo {
    overflow-y: scroll;
    padding-left: 7vh;
    cursor: pointer;
    max-height: 6cm;
    transition: color var(--transition-time), background-color var(--transition-time), height var(--transition-time);
}

.backupInfo::-webkit-scrollbar {
    display: none;
}

.backupInfo {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.backupInfo.inactive {
    height: 0;
}

.closeSection {
    z-index: 1;
}

.items>div:not(.backupInfo) {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding-left: 14%;
    width: 100%;
    position: relative;
    transition: color var(--transition-time), background-color var(--transition-time), height var(--transition-time);
    box-sizing: border-box;
}

.items>div:not(.backupInfo):hover {
    background-color: var(--appColor);
    color: white;
    
}

.items>div:not(.backupInfo):hover img {
    filter: brightness(0) invert(1);
}

.items img {
    height: var(--smallHeight);
    min-height: var(--minIconHeight);
    max-height: 0.8cm;
    margin-right: 2vh;
    transition: filter var(--transition-time);
}

.items>div>p {
    white-space: nowrap;
}

.paperbin img:nth-child(3) {
    height: 2vh;
    min-height: 0;
    left: 10vh;
    animation-fill-mode: forwards;
    animation-duration: 400ms;
}

.return {
    animation-name: return;
}

@keyframes return {
    0% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(0);
    }
}

#sidebar {
    border-left: var(--emergentBorder);
    height: 100vh;
    width: 0;
    max-width: 10cm;
    background-color: white;
    transition-property: right;
    transition: var(--fastTransition);
    box-sizing: border-box;
    position: absolute;
    right: -100%;
    overflow: hidden;
    top: 0;
    right: 0;
    z-index: 2;
}

#sidebar .closeIcon {
    position: absolute;
    left: 2vh;
    top: 2vh;
    height: var(--largeHeight);
    min-height: var(--minCloseIconHeigh);
    cursor: pointer;
}

#sidebar .items {
    top: 13vh;
    position: relative;
}

#sidebarContainer {
    top: 0;
    right: 0;
    z-index: 401;
    width: 14%;
    height: 9vh;
    min-height: 1cm;
    max-width: 2cm;
    position: absolute;
    box-sizing: border-box;
}

#sidebarContainer .active {
    width: 80vw;
    transition: var(--slowTransition);
}

.switch {
    margin-left: 10px;
}

.turn {
    animation-name: turn;
}

@keyframes turn {
    100% {
        transform: rotate(180deg);
    }
}